@charset "UTF-8";
@use "sass:map";

// ================================================================
// ブレークポイント変数
// ================================================================
//マップ型変数を定義
$breakpoints: (
  "sp-xs": "screen and (max-width: 375px)",
  "sp-ss": "screen and (max-width: 500px)",
  "sp-s": 'screen and (max-width: 567px)',
  "sp-m": 'screen and (min-width: 568px) and (max-width: 767px)',
  "tb": "screen and (min-width: 768px) and (max-width: 1024px)",
  // "pc-m": "screen and (min-width: 1025px) and (max-width: 1840px)",
  // "pc-l": "screen and (min-width: 1841px)",
  // モバイル（スマホ＆タブレット）の表示
  "sp": "screen and (max-width: 767px)",
  // PC（PC）の表示
  "pc": "screen and (min-width: 768px)" // タブレットの表示
  // "tab": "screen and (max-width: 767px)",,,,
) !default;

// ================================================================
// mixin
// ================================================================
// ----------------------------------------------------------------
//メディアクエリ用mixin
// ----------------------------------------------------------------
@mixin mq($breakpoint: pc) {
  //map-get(マップ型変数, キー)で値を取得
  @media #{map.get($breakpoints, $breakpoint)} {
    //この中をカスタムできる
    @content;
  }
}

/*----------------------------------
  hover用mixin
  hoverはPCのみ
  IE11対応
-----------------------------------*/
@mixin hover {
  //hoverはPCのみ
  @media (hover: hover) and (pointer: fine) {
    &:hover {
      @content;
    }
  }
  //IE11対応用
  @media (-ms-high-contrast: none), (-ms-high-contrast: active) {
    &:hover {
      @content;
    }
  }
}
