@use "global" as *;

.detail-inner {
  margin: 0 auto;
  padding: 0 rem(220);

  @include mq(sp) {
    padding: 0 rem(10);
    margin-bottom: rem(50);
  }
}

.portfolio-detail {
  background: $white;
  outline: 0.5px solid #333;
  max-width: 1000px;
  margin: 0 auto;
  padding: rem(80) rem(50) rem(80);

  @include mq(sp) {
    padding: rem(40) rem(10);
    margin: 0 auto;
  }
}

.pf-meta {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: rem(8);
  margin-top: rem(5);
  margin-bottom: rem(26);

  @include mq(sp) {
    position: relative;
    margin-bottom: rem(20);
  }
}
.pf-date {
  display: flex;
  align-items: flex-start;
  font-family: $second-font;
  font-size: rem(13);
  color: $black;
}

input[type="checkbox"] {
  display: none;
}
.favorite {
  position: relative;
  cursor: pointer;
  margin-left: auto;
  margin-right: rem(8);

  svg {
    width: rem(30);
    height: rem(30);
    fill: #1e1e1e;
  }

  label {
    position: relative;
    display: block;
    cursor: pointer;
    width: rem(30);
    height: rem(30);
  }

  @include mq(sp) {
    position: absolute;
    width: rem(30);
    height: rem(30);
    top: rem(-50);
    right: 0;
    margin-left: 0;
    z-index: 2;

    svg {
      width: 100%;
      height: 100%;
    }
  }
}

input[type="checkbox"] + label svg {
  fill: #333;
  opacity: 0.3;
  transition: all 0.3s;
}

input[type="checkbox"]:checked + label svg {
  fill: #ff6d6d;
  opacity: 1;
  animation: like 0.8s ease-out;
  transition: all 0.3s;
}

@keyframes like {
  0% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(0.5, 0.5);
  }
  100% {
    transform: scale(1, 1);
  }
}

input[type="checkbox"]:checked + label::after {
  content: "";
  position: absolute;
  width: rem(50);
  height: rem(50);
  border: 2px solid #fa9797;
  border-radius: 50%;
  animation: circle 0.8s ease-out;
  opacity: 0;
  top: rem(-10);
  left: rem(-10);
  z-index: 1;

  @include mq(sp) {
    width: rem(30);
    height: rem(30);
    border: 1px solid #fa9797;
    top: rem(0);
    left: rem(0);
  }
}

@keyframes circle {
  0% {
    transform: scale(0.2, 0.2);
  }
  50% {
    transform: scale(0.5, 0.5);
  }
  80% {
    transform: scale(1, 1);
    opacity: 0.8;
  }
  100% {
    transform: scale(1.3, 1.3);
    opacity: 0;
  }
}

.thumbnail {
  border: 1px solid $black;
  width: 100%;
  max-width: rem(900);
  aspect-ratio: 900 / 470;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: rem(20);
  font-size: rem(24);
  color: #333;

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  @include mq(sp) {
    aspect-ratio: 335 / 200;
    font-size: rem(16);
  }
}

.site-url {
  color: $black;
  margin-bottom: rem(40);
  font-size: rem(16);
}

.user-row {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: rem(20);

  @include mq(sp) {
    gap: rem(10);
  }
}

.pf-icon {
  width: rem(80);
  height: rem(80);
  border-radius: 50%;
  background: #d9d9d9;
  object-fit: cover;
  display: flex;
  align-items: center;
  justify-content: center;

  @include mq(sp) {
    width: rem(50);
    height: rem(50);
  }
}

.user-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.user-name {
  font-family: $base-font;
  font-size: rem(32);
  @include mq(sp) {
    font-size: rem(18);
  }
}
.user-role {
  font-family: $base-font;
  font-size: rem(24);
  margin-top: rem(8);

  @include mq(sp) {
    font-size: rem(16);
  }
}

.pf-xicon {
  margin-left: auto;
  margin-top: rem(50);
  width: rem(40);
  height: rem(40);

  @include mq(tb) {
    margin-top: 0;
  }
  @include mq(sp) {
    margin-top: 0;
  }

  img {
    width: rem(40);
    height: rem(40);
    max-width: 100%;
    display: block;
  }
}

.point-box {
  border: 0.3px solid $main;
  padding: rem(30) rem(20) rem(24);
  margin-top: rem(40);
  margin-bottom: rem(26);

  @include mq(sp) {
    padding: rem(20) rem(10) rem(30);
    margin-top: rem(24);
    margin-bottom: rem(30);
  }
}

.point-title {
  margin-bottom: rem(10);
  font-size: rem(24);
}

.point-text {
  font-size: rem(16);
}

.comment-box {
  padding: rem(30) rem(22);
  background: $main;
  color: $white;

  @include mq(sp) {
    padding: rem(30) rem(10) rem(30);
  }
}

.comment-title {
  margin-bottom: rem(8);
  font-size: rem(24);
}

.comment-text {
  font-size: rem(16);
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: rem(115);
  width: 100%;
  padding-block: rem(80);

  @include mq(sp) {
    flex-direction: column;
    gap: rem(30);
    margin: rem(58) auto rem(66);
    padding-block: 0;
  }
}

.prev,
.next {
  display: flex;
  align-items: center;
  font-family: $second-font;
  font-size: rem(24);
  cursor: pointer;
  gap: rem(30);

  @include mq(sp) {
    font-size: rem(18);
    gap: rem(20);
  }

  &:hover {
    .prev-border,
    .next-border {
      background: transparent;

      &::before {
        transform: scale(1, 1);
      }
    }
  }
}

.prev-border,
.next-border {
  width: 50px;
  height: 1px;
  background: #888;
  display: inline-block;
  position: relative;
  transition: background 0.3s;

  &::before {
    content: "";
    width: 75px;
    height: 1px;
    position: absolute;
    bottom: 0;
    transform-origin: right top;
    transform: scale(0, 1);
    transition: transform 0.5s;
    background: linear-gradient(90deg, #333, #fff);
  }
}
.prev-border {
  &::before {
    right: 0;
  }
}

.next-border {
  &::before {
    left: 0;
    transform-origin: left top;
    background: linear-gradient(90deg, #fff, #333);
  }
}

.prev-text,
.next-text {
  color: #333 !important;
  text-decoration: none !important;
}

.pfsection-line {
  position: relative;
  width: 75%;
  height: 1px;
  background: $main;
  margin: 0 auto;
  z-index: 1;
  @include mq(sp) {
    margin: 0 auto;
  }
}

.pf-gallery {
  background: $bg;
  padding-block: rem(80) rem(132);

  @include mq(sp) {
    padding: rem(60) 0 rem(120);
  }
}

.pf-gallery__inner {
  max-width: rem(700);
  width: 100%;
  margin: 0 auto;

  @include mq(sp) {
    max-width: rem(335);
    padding-inline: rem(10);
  }
}

.pf-gallery__wrapper:nth-of-type(2) {
  margin-top: rem(60);
}

.pf-card-section-title {
  max-width: rem(670);
  display: inline-block;
  font-family: $second-font;
  font-weight: 400;
  font-size: rem(24);
  margin-bottom: rem(16);
  width: 100%;
  align-self: flex-start;

  @include mq(sp) {
    max-width: rem(335);
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
}

.pf-gallery__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: rem(20);

  @include mq(tb) {
    grid-template-columns: repeat(3, 1fr);
  }

  @include mq(sp) {
    grid-template-columns: 1fr;
  }
}

.pf-gallery__card {
  background: white;
  box-shadow: 2px 2px 4px 0px rgba(51, 51, 51, 0.5);
  opacity: 0;
  width: 100%;
  margin-inline: auto;
  padding: rem(10);
  outline: 1px solid #333;
  display: flex;
  flex-direction: column;
}

.pf-gallery__img {
  aspect-ratio: 200/104;
  transition: all 0.3s ease 0s;
}

.pf-gallery__card:hover .pf-gallery__img {
  transform: scale(1.05);
}

.pf-gallery__body {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: rem(10);
  gap: rem(20);
}

.pf-gallery__icon {
  width: rem(36);
  height: auto;
  border-radius: 50%;
}

.pf-gallery__info {
  display: flex;
  flex-direction: column;
  gap: rem(5);
}

.pf-gallery__name {
  font-size: rem(13);
  line-height: 1.25;
  color: #333;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.pf-gallery__type {
  font-size: rem(13);
  color: #333;
}
