@use "global" as *;

.label-group {
  display: flex;
  gap: rem(10);
  flex-wrap: wrap;
  justify-content: center;

  @include mq(sp) {
    justify-content: flex-start;
  }
}

#detail .label {
  &:hover {
    opacity: 0.8;
    color: #fff;
  }
}

#detail .label.new {
  &:hover {
    opacity: 0.8;
    color: #d00;
  }
}

.gallery-label__wrap {
  margin-top: rem(20);
  justify-content: left;

  .label {
    font-size: rem(10);
    width: rem(60);
    padding-block: rem(3);
  }
}

.label {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: $second-font;
  padding: rem(2) rem(8);
  font-size: rem(12);
  width: rem(70);
  border-radius: 2px;
  border: 1px solid $main;
  color: $white;
  text-shadow: 1px 1px 0 #5a5a5a, -1px 1px 0 #5a5a5a, 1px -1px 0 #5a5a5a, -1px -1px 0 #5a5a5a, 0 1px 0 #5a5a5a, 1px 0 0 #5a5a5a, 0 -1px 0 #5a5a5a, -1px 0 0 #5a5a5a;
  font-weight: 600;
  line-height: 1.1;
  position: relative;
  transition: all 0.3s ease;

  &.new {
    background: $white;
    color: #d00;
    text-shadow: none;
    border: 1px solid #d00;
  }
  &.type {
    background: #333;
    letter-spacing: 0;
  }
  &.style {
    background: #d9d9d9;
  }
  &.red {
    background: #f00;
  }
  &.orange {
    background: #f90;
  }
  &.yellow {
    background: #ff0;
  }
  &.green {
    background: #008000;
  }
  &.blue {
    background: #00f;
  }
  &.purple {
    background: #90c;
  }
  &.pink {
    background: #ff69b4;
  }
  &.brown {
    background: #8b4513;
  }
  &.black {
    background: #000;
  }
  &.gray {
    background: #a5a5a5;
  }
  &.white {
    background: #fff;
  }
  &.abc {
    overflow: hidden;
  }
}

.color-other-img {
  position: absolute;
  top: 0;
  left: 0;
  width: rem(100);
  height: rem(70);
  rotate: -45deg;
  transform: translate(30%, -22%);
}

.color-other-text {
  z-index: 1;
}
